/*@import url("/dist/fonts/flaticon/flaticon.css");*/

@import "https://fonts.googleapis.com/css?family=Roboto:400,300,500,700,900";
@import "https://fonts.googleapis.com/css?family=Great+Vibes";
@import "components/mixins";
@import "components/variables";

/* ================= General ==================== */

html {
    font-family: 'Roboto', sans-serif !important;
}

html,
body {
    height: 100%;
    margin: 0;
}

body {
    height: 100%;
    margin: 0;
    display: flex;
    flex-direction: column;
    font-family: 'Roboto', sans-serif !important;
    font-size: 13px;
    line-height: 29px;
    font-weight: 500;
    overflow-x: hidden;
    -webkit-text-size-adjust: 100%;
    -webkit-overflow-scrolling: touch;
    -webkit-font-smoothing: antialiased !important;
    color: $bourbon;
    background-color: #CCC2A4;
}

.topwrap {
    flex: 1 0 auto;
    overflow: hidden;
}
.no-js #loader {
  display: none;
}

.js #loader {
  display: block;
  position: absolute;
  left: 100px;
  top: 0;
}
.se-pre-con {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background: url(/dist/img/loading.svg) center no-repeat $primary-color;
}
.page {
  background-size: cover;
  background-position: center center;
  .page-image {
    background-size: cover;
    background-position: center center;
  }
}

section {
    &:nth-of-type(odd) {
        background-color: $primary-color;
    }
}

.projects-bg {
    background-color: #765F3C;
}

header {
    background-color: $primary-color;
}

.footer {
    flex-shrink: 0;
}

.secondary-footer {
    background-color: $bourbon;
    border-top: 12px solid $tertiary-color;
    * {
        color: $tertiary-color !important;
    }
}

.secondary-header {
    background-color: $tertiary-color;
    * {
        color: eeeeee;
    }
}

a {
    color: $bourbon;
    outline: 0;
    cursor: pointer;
    text-decoration: none;
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
    >* {
        outline: 0;
        cursor: pointer;
        text-decoration: none;
    }
    &:focus {
        color: $topnav;
        outline: 0;
        text-decoration: none;
    }
    &:hover {
        color: $topnav;
        outline: 0;
        text-decoration: none;
    }
}

.navbar {
    a {
        -webkit-transition: all 0.3s ease;
        -moz-transition: all 0.3s ease;
        -ms-transition: all 0.3s ease;
        -o-transition: all 0.3s ease;
        transition: all 0.3s ease;
    }
}

.form-control {
    -webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
    border-radius: 0;
}

