    display: none;
}

.boxed {
    border: 1px dotted goldenrod;
    margin-top: 55px;
    padding: 12px;
    background-color: #ffffff;
}

.gallery-thumb {
    .flex-direction-nav {
        a.flex-next {
            &::before {
                font: 20px 'Material Icons';
                color: $bourbon !important;
                font: 20px 'Material Icons';
                color: #fff;
                content: 'arrow_forward';
            }
        }
        a.flex-prev {
            &::before {
                font: 20px 'Material Icons';
                color: $bourbon !important;
                font: 20px 'Material Icons';
                color: #fff;
                content: 'arrow_back';
            }
        }
        a {
            opacity: 1;
            top: 768px;
        }
        .flex-prev {
            left: 18px;
        }
        .flex-next {
            right: 18px;
        }
    }
    .flex-viewport {
        li {
            img {
                width: 100%;
            }
        }
    }
    .flex-control-thumbs {
        li {
            width: 70px;
            float: none;
            margin: 0 5px;
        }
        img {
            width: 70px;
            height: 70px !important;
            border-radius: 50%;
            background: #fff;
            padding: 5px;
            opacity: 1;
            z-index: 100;
            position: relative;
        }
    }
}

.flexslider {
    margin: 0 0 30px;
    background: transparent;
    border: none;
    position: relative;
    zoom: 1;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
    -webkit-box-shadow: '' 0 1px 4px rgba(0, 0, 0, 0.2);
    -moz-box-shadow: '' 0 1px 4px rgba(0, 0, 0, 0.2);
    -o-box-shadow: '' 0 1px 4px rgba(0, 0, 0, 0.2);
    box-shadow: '' 0 1px 4px rgba(0, 0, 0, 0.2);
}

.flex-control-thumbs {
    img {
        border-radius: 50%;
        cursor: pointer;
        border: 2px solid #656A68;
        width: 60%;
        margin: 0 auto;
        padding: 0;
        &:hover {
            border: 2px solid $primary-color;
            border-radius: 40%;
        }
    }
    .flex-active {
        border: 2px solid $bourbon;
        border-radius: 40%;
    }
    li {
        width: 15%;
        float: none;
        margin: 0;
    }
}

.flex-caption {
    width: 96%;
    padding: 2%;
    left: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .5);
    color: #fff;
    text-shadow: 0 -1px 0 rgba(0, 0, 0, .3);
