        &:active {
            outline: 0;
        }
        &:focus {
            outline: 0;
        }
    }
    .icon-search {
        -webkit-transition: 0.2s ease-in-out;
        -moz-transition: 0.2s ease-in-out;
        -o-transition: 0.2s ease-in-out;
        transition: 0.2s ease-in-out;
    }
    .search-close {
        -webkit-transition: 0.2s ease-in-out;
        -moz-transition: 0.2s ease-in-out;
        -o-transition: 0.2s ease-in-out;
        transition: 0.2s ease-in-out;
    }
}

.tt-nav.sticky {
    .search-trigger {
        i.material-icons {
            line-height: 62px;
        }
    }
}

.icon-search {
    position: absolute;
    top: 18px;
    left: 20px;
    font-size: 28px;
}

.search-close {
    position: fixed;
    top: 35px;
    right: 35px;
    color: rgba(255, 255, 255, 0.9);
    font-size: 30px;
    visibility: hidden;
    -webkit-transform: translate(10px, 0) rotate(90deg);
    -moz-transform: translate(10px, 0) rotate(90deg);
    -ms-transform: translate(10px, 0) rotate(90deg);
    -o-transform: translate(10px, 0) rotate(90deg);
    transform: translate(10px, 0) rotate(90deg);
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    z-index: 1001;
    cursor: pointer;
    &:hover {
        color: #fff;
    }
}

.search-form-wrapper {
    position: relative;
    .search-button {
        position: absolute;
        top: 30px;
        right: 0;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -o-border-radius: 50%;
        border-radius: 50%;
        width: 40px;
        height: 40px;
        font-size: 14px;
        text-align: center;
        line-height: 40px;
        padding: 0;
    }
    .input-field {
        label {
            color: #fff;
            font-size: 17px;
        }
        input[type=text] {
            color: #fff;
            font-size: 17px;
        }
        label.active {
            font-size: 12px;
        }
    }
    form {
        position: absolute;
        top: 150px;
        left: 0;
        right: 0;
        bottom: 0;
        display: block;
        pointer-events: none;
        -moz-opacity: 0;
        -webkit-opacity: 0;
        opacity: 0;
        visibility: hidden;
        -webkit-transform: translate(40px, 0);
        -moz-transform: translate(40px, 0);
        -ms-transform: translate(40px, 0);
        -o-transform: translate(40px, 0);
        transform: translate(40px, 0);
        -webkit-transition: all 0.3s ease-in-out;
        -moz-transition: all 0.3s ease-in-out;
        -ms-transition: all 0.3s ease-in-out;
        -o-transition: all 0.3s ease-in-out;
        transition: all 0.3s ease-in-out;
        z-index: 1000;
    }
}

.active-search {
    overflow: hidden;
    .search-form-wrapper {
        form {
            -moz-opacity: 1;
            -webkit-opacity: 1;
            opacity: 1;
            visibility: visible;
            pointer-events: all;
            -webkit-transform: none;
            -moz-transform: none;
            -ms-transform: none;
            -o-transform: none;
            transform: none;
        }
    }
    .search-close {
        -moz-opacity: 1;
        -webkit-opacity: 1;
        opacity: 1;
        visibility: visible;
        pointer-events: all;
        -webkit-transform: none;
        -moz-transform: none;
        -ms-transform: none;
        -o-transform: none;
        transform: none;
    }
    .search-trigger {
        .icon-search {
            -moz-opacity: 0;
            -webkit-opacity: 0;
            opacity: 0;
            visibility: hidden;
        }
        .search-btn {
            position: relative;
            cursor: default;
            z-index: 300;
            background-color: rgba(3, 169, 244, 0.9);
            -webkit-transform: scale(90);
            -moz-transform: scale(90);
            -ms-transform: scale(90);
            -o-transform: scale(90);
            transform: scale(90);
        }
    }
}

#preloader {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(221, 208, 192, .999);
    z-index: 999999;
    text-align: center;
    .preloader-position {
        width: 100%;
        margin: 0 auto;
        left: 50%;
        position: absolute;
        top: 50%;
        -webkit-transform: translate(-50%, -50%);
        -moz-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
    }
}

.progress {
